import { Button } from "antd";
import classNames from "classnames";
import React from "react";
import "./index.scss";

interface SimpleProps {
  containerClassName?: string;
  className?: string;
  disabled?: boolean;
  width?: string;
  title?: string | JSX.Element;
  style?: StratosNodeMonitor.SimpleObject;
  handleClick: (e?: any) => void;
}

const defaultButtonWidth = "200px";

const RoundedButton: React.FC<SimpleProps> = ({
  containerClassName,
  className,
  disabled = false,
  width,
  title,
  style = {},
  handleClick,
}) => {
  return (
    <div className={classNames("button-container", containerClassName)}>
      <Button
        style={{ ...style, width: width || defaultButtonWidth }}
        className={classNames("btn-submit", className)}
        disabled={disabled}
        size="large"
        htmlType="button"
        onClick={handleClick}
      >
        {title}
      </Button>
    </div>
  );
};

export default RoundedButton;
